import { ensureError } from "../common/errorHelpers";
import { getTimeMs } from "../common/time";
import { TypedCustomEvent } from "../events/TypedCustomEvent";
import { TypedEventTarget } from "../events/TypedEventTarget";
import { ChainableHandler, RequestMetadata } from "./HandlerChainBuilder";

let requestId = 0;
const safeParseInt = (str: string | null) => {
    if (str == null) return 0;
    const maybeInt = parseInt(str);
    return isNaN(maybeInt) ? 0 : maybeInt;
};

interface Started {
    requestId: number;
    timeMs: number;
    dimensions: Dimensions;
}

interface Completed {
    requestId: number;
    timeMs: number;
    dimensions: Dimensions;
    status: number;
    sizeByte: number;
}

interface Errored {
    requestId: number;
    timeMs: number;
    dimensions: Dimensions;
    error: Error;
}

export type Dimensions = Record<string, string | undefined>;

export type RequestStateEvents =
    | TypedCustomEvent<"started", Started>
    | TypedCustomEvent<"completed", Completed>
    | TypedCustomEvent<"errored", Errored>;

export const requestStateEventTarget = new TypedEventTarget<RequestStateEvents>();

export const dispatchRequestStarted = (data: Omit<Started, "requestId" | "timeMs">): Started => {
    const started: Started = { ...data, requestId: requestId++, timeMs: getTimeMs() };
    requestStateEventTarget.dispatchEvent(new TypedCustomEvent("started", started));
    return started;
};

export const dispatchRequestCompleted = (data: Omit<Completed, "timeMs">): Completed => {
    const completed: Completed = { ...data, timeMs: getTimeMs() };
    requestStateEventTarget.dispatchEvent(new TypedCustomEvent("completed", completed));
    return completed;
};

export const dispatchRequestErrored = (data: Omit<Errored, "timeMs">): Errored => {
    const errored: Errored = { ...data, timeMs: getTimeMs() };
    requestStateEventTarget.dispatchEvent(new TypedCustomEvent("errored", errored));
    return errored;
};

export const createRequestStateEmittingHandler =
    <D extends Dimensions = Dimensions>(): ChainableHandler<
        [RequestInfo, D],
        Response,
        RequestInfo,
        Response,
        RequestMetadata
    > =>
    (next) =>
    async ([request, dimensions], metadata) => {
        const { requestId } = dispatchRequestStarted({ dimensions });
        try {
            const response = await next(request, metadata);
            const status = response.status;
            const sizeByte = safeParseInt(response.headers.get("content-length"));
            dispatchRequestCompleted({ requestId, dimensions, status, sizeByte });
            return response;
        } catch (error) {
            dispatchRequestErrored({ requestId, dimensions, error: ensureError(error) });
            throw error;
        }
    };
